.section__advantages {
  max-width: unset;
  background-color: #f4f5f7;
}

.section__advantages .content {
  padding: 64px var(--section-padding);
  max-width: var(--section-width);
  flex-direction: column;
  align-items: center;
  margin: auto;
  display: flex;
}

.section__advantages .section--heading {
  font-size: 32px;
  font-weight: var(--fw-light);
  letter-spacing: -.57px;
  color: var(--dark);
  text-align: center;
  max-width: 12ch;
}

.section__advantages .section--description {
  font-size: 15px;
  font-weight: var(--fw-medium);
  letter-spacing: -.23px;
  color: var(--greyish--blue);
  text-align: center;
  max-width: 330px;
  margin-top: 14px;
  line-height: 25px;
}

.section__advantages .container {
  flex-direction: column;
  align-items: center;
  gap: 32px;
  width: 100%;
  margin-top: 56px;
  display: flex;
  overflow: hidden;
}

.advantage {
  flex-direction: column;
  align-items: center;
  display: flex;
}

.advantage--heading {
  font-size: 20px;
  font-weight: var(--fw-light);
  letter-spacing: -.36px;
  color: var(--dark);
  margin-top: 24px;
  line-height: 28px;
}

.advantage--description {
  font-size: 15px;
  font-weight: var(--fw-medium);
  letter-spacing: -.23px;
  color: var(--greyish--blue);
  text-align: center;
  max-width: 330px;
  margin-top: 16px;
  line-height: 25px;
}

@media screen and (width >= 550px) {
  .section__advantages .container {
    grid-template-columns: 1fr 1fr;
