    gap: 30px;
    display: grid;
  }

  .section__advantages .section--heading, .section__advantages .section--description {
    max-width: 635px;
  }

  .advantage--description {
    text-align: center;
  }
}

@media screen and (width >= 1100px) {
  .section__advantages .content {
    align-items: start;
  }

  .section__advantages .container {
    grid-template-columns: 1fr 1fr 1fr 1fr;
    align-items: start;
  }

  .section__advantages .section--heading {
